body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}*{box-sizing:border-box}html,body,ul,li,ol,dl,dd,dt,p,h1,h2,h3,h4,h5,h6,form,fieldset,legend,img{margin:0;padding:0}fieldset,img,input,button{border:none;padding:0;margin:0;outline-style:none}ul,ol{list-style:none}input{padding-top:0;padding-bottom:0;font-family:"SimSun","宋体"}select,input{vertical-align:middle}select,input,textarea{font-size:12px;margin:0}textarea{resize:none}img{border:0;vertical-align:middle}table{border-collapse:collapse}body{font:12px/150% Arial,Verdana,"宋体";color:#666;background:#fff}.clearfix:before,.clearfix:after{content:"";display:table}.clearfix:after{clear:both}.clearfix{*zoom:1}a{color:#666;text-decoration:none}a:hover{color:#c81623}h1,h2,h3,h4,h5,h6{text-decoration:none;font-weight:normal}s,i,em{font-style:normal;text-decoration:none}.col-red{color:#c81623 !important}.App{font-family:Arial,Helvetica,sans-serif}.show{display:block}.hidden{display:none}